.SearchItem {
  border-bottom: .1rem solid var(--light-grey-color);
  height: 6rem;
  padding: 1.5rem 1rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  cursor: pointer;
}

.SearchItem:hover {
  background-color: var(--lightest-grey-color);
}

.SearchItem.SearchItem_matched {
  background: var(--light-green-color);
}

.SearchItem:last-child {
  border-bottom: none;
}

.SearchItem__image-and-shortcut {
  line-height: 3rem;
  display: -ms-flexbox;
  display: flex;
}

.SearchItem__image {
  width: 3rem;
  height: 3rem;
  margin-right: 1rem;
}

.SearchItem__shortcut {
  font-weight: 700;
  font-size: 2rem;
}

.SearchItem__description {
  line-height: 3rem;
  color: var(--grey-color);
}

.SearchItem.SearchItem_matched .SearchItem__description {
  color: var(--green-color);
}



